.app {
  margin: 10px;

}

.add-task {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
  background-color: blue;
  height: 100px;
}

.input-task {
  border-color: blue;
  border-radius: 4px;
  margin: 0 10px;
  padding: 15px;
  background-color: white;
  height: 40px;
}

.add-task button {
  height: 40px;
}

.list {
  text-align: center
}

.list-item {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;

}

.list-item p {
  font-weight: bold;
  font-size: larger;
}

.list-item * {
  margin: 20px 10px;
}



.list-item button {

  background-color: orange;
  padding: 2px;
  border: none;
  opacity: 0.6;

}